import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { UtilsProvider } from '../../providers/utils/utils';

/**
 * Generated class for the AmountToPayPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-amount-to-pay',
  templateUrl: 'amount-to-pay.html',
})
export class AmountToPayPage {

  relationship: string;
  value: string;
  valor: string
  condicion: boolean;
  public cuentasPropias: string;
  public valorMini: string;
  public valorTotal: string;
  public valorSeleccion: string;
  public selectedAccount: string;
  public minimo: string;
  public total: string;
  public cuot: string = 'CUOT';
  public tota: string = 'TOTA';
  public otro: string = 'OTRO';
  public split: any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private storage: Storage, public utils: UtilsProvider) {
    this.condicion = true;

    //Obtención de las cuentas a debitar
    this.storage.get('cuentasPropias').then((valor) => {
      this.cuentasPropias = valor;
    });


    //Obtencion de valor minimo y total de la tarjeta de credito -> se obtiene de ProductOverView
    //this.storage.get('selectedAccount').then((val) => {
      this.storage.get('creditLst').then((val) => {
      this.selectedAccount = val[0];
     // alert("-> "+this.selectedAccount);
      this.minimo = this.selectedAccount[5];
      this.total = this.selectedAccount[3];
     
    });

    
  }

  ionViewDidLoad() {

    console.log('ionViewDidLoad AmountToPayPage');
/*
    this.storage.get('creditLst').then((val) => {
      this.selectedAccount = val;
      alert("-> "+this.selectedAccount);
      this.minimo = this.selectedAccount[5];
      this.total = this.selectedAccount[3];
    });
*/
  }

  //Metodo de validación de la selección del radio button
  validacion(){
    if (this.relationship == "Minimum payment") {
      this.valorSeleccion = this.minimo;
      this.condicion = true;
      this.valor = "";
      this.storage.set('cuot', this.cuot);
      //alert("Minimum payment");
    }
    else if (this.relationship == "full amount") {
      this.valorSeleccion = this.total;
      this.condicion = true;
      this.valor = "";
      this.storage.set('tota', this.tota);
      //alert("full amount");
    }
    else {
      this.condicion = false;
      this.valorSeleccion = (this.valor != undefined) ? this.valor:"Error";
      this.storage.set('otro', this.otro);
      //alert("other");
    }
    return this.valorSeleccion;
    
  }

  seleccion(){
    
    if(this.validacion() != "Error"){
      this.storage.set('valorSeleccionado', this.validacion());
      this.navCtrl.push("ProductToPayPage");
    }
    else {
      this.condicion = true;
      this.utils.alertCampos("amountToPay.alert");
    }
    
  }

  select(){
    this.validacion();
  }

  logout(){
    this.utils.presentConfirm("LoginPage");
    //this.utils.logout();
  }

}



